@import url("https://fonts.googleapis.com/css2?family=Jost:wght@200;300;400&display=swap");
.design-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  min-height: 100vh;
  /* padding: 100px 0; */
  font-family: Jost;
  margin-top: 50px;
}

.design {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: right;
  justify-content: right;
}

.time-right {
  float: right;
  background-color: black;
  color: white;
  width: 170px;
  height: 30px;
  border-radius: 20px;
  margin-right: 15px !important;
}

.time-left {
  float: left;
  background-color: black;
  color: white;
  width: 170px;
  height: 30px;
  border-radius: 20px;
  margin-left: 15px !important;
}
.timeline {
  width: 80%;
  height: auto;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.timeline-content {
  padding: 20px;
  background: #1f1f1f;
  /* -webkit-box-shadow: 5px 5px 10px #1a1a1a, -5px -5px 10px #242424; */
  /* box-shadow: 5px 5px 10px #1a1a1a, -5px -5px 10px #242424; */
  border-radius: 5px;
  color: white;
  padding: 1.75rem;
  transition: 0.4s ease;
  overflow-wrap: break-word !important;
  margin: 1rem;
  margin-bottom: 20px;
  border-radius: 6px;
  transition: all 0.4s;
}
.timeline-content:hover {
  box-shadow: 5px 5px 10px #797878, -5px -5px 10px #676767;
}

.track-image {
  max-width: 35%;
  height: auto;
  border-radius: 50%;
}

.timeline-component {
  margin: 0px 20px 20px 20px;
}
@media screen and (min-width: 768px) {
  .timeline {
    display: grid;
    grid-template-columns: 1fr 3px 1fr;
  }
  .timeline-middle {
    position: relative;
    background-image: linear-gradient(45deg, #f27121, #e94057, #8a2387);
    width: 3px;
    height: 100%;
  }
  .main-middle {
    opacity: 0;
  }
  .timeline-circle {
    position: absolute;
    top: 0;
    left: 50%;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-image: linear-gradient(45deg, #f27121, #e94057, #8a2387);
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
  }
}

